import React, { Fragment } from "react";
import {Link} from 'react-router-dom';

import Navegation from '../components/Navegation'

const Data = () => {
  return (

    <Fragment>
        <Navegation/>

        <div className="uk-grid uk-child-width-1-1@s uk-text-center uk-margin-medium uk-animation-fade" uk-grid="true">
          <div>
              <h2 className="uk-margin-small uk-text-uppercase uk-text-bold">Modulos Pilares</h2>
              <h4 className="uk-margin-small">Gestión general de modulos primarios</h4>
          </div>
        </div>

        <div className="uk-child-width-1-3@m uk-padding-large uk-padding-remove-top" uk-grid="true" uk-scrollspy="cls: uk-animation-fade; target: .uk-card; delay: 300; repeat: true">
          <div>
            <div className="uk-card uk-card-default">
                <div className="uk-card-media-top">
                  <div className="uk-inline-clip uk-transition-toggle uk-light">
                    <Link to="/dashboard-clientes">
                      <img src="static/images/light.png" alt=""/>
                      <div className="uk-position-center">
                          <span className="uk-transition-fade uk-text-secondary" uk-icon="icon: plus; ratio: 3"></span>
                      </div>
                    </Link>
                  </div>
                </div>
                <div className="uk-card-body uk-text-center uk-margin">
                    <h4 className="uk-margin-small uk-text-bold">Mis Clientes</h4>
                    <p>Importa, exporta e imprimí todos tus clientes actuales.</p>
                </div>
            </div>
          </div>
          <div>
            <div className="uk-card uk-card-default">
                <div className="uk-card-media-top">
                  <div className="uk-inline-clip uk-transition-toggle uk-light">
                    <Link to="/dashboard-productos">
                      <img src="static/images/light.png" alt=""/>
                      <div className="uk-position-center">
                          <span className="uk-transition-fade uk-text-secondary" uk-icon="icon: plus; ratio: 3"></span>
                      </div>
                    </Link>
                  </div>
                </div>
                <div className="uk-card-body uk-text-center uk-margin">
                    <h4 className="uk-margin-small uk-text-bold">Mis Productos</h4>
                    <p>Importa, exporta e imprimí todos tus productos actuales.</p>
                </div>
            </div>
          </div>
          <div>
            <div className="uk-card uk-card-default">
                <div className="uk-card-media-top">
                  <div className="uk-inline-clip uk-transition-toggle uk-light">
                    <Link to="/dashboard-rubros">
                      <img src="static/images/light.png" alt=""/>
                      <div className="uk-position-center">
                          <span className="uk-transition-fade uk-text-secondary" uk-icon="icon: plus; ratio: 3"></span>
                      </div>
                    </Link>
                  </div>
                </div>
                <div className="uk-card-body uk-text-center uk-margin">
                    <h4 className="uk-margin-small uk-text-bold">Categoria de SubRubros</h4>
                    <p>Importa, exporta e imprimí todos los subrubros de tus productos.</p>
                </div>
            </div>
          </div>
        </div>

        <div id="offcanvas-nav-primary" uk-offcanvas="mode: push;overlay: true">
            <div className="uk-offcanvas-bar uk-flex uk-flex-column">

                <ul className="uk-nav uk-nav-primary uk-nav-center uk-margin-auto-vertical">
                    <li className="uk-active">
                    <img className="uk-border-circle uk-padding-small" src="../static/images/avatar.jpg" width="200" height="200" alt="Border circle"/>
                    </li>
                    <li className="uk-nav-header">Menú</li>
                    <li><Link to="#"> Mi Perfil</Link></li>
                    <li><Link to="#"> Suscripción</Link></li>
                    <li><Link to="#"> Configuración</Link></li>
                    <li className="uk-nav-divider"></li>
                    <li><Link to="#">Cerrar Sesión</Link></li>
                </ul>

            </div>
        </div>

    </Fragment>

  );
};

export default Data;
